
import style from '../../css/view/design.module.less'
import '../../css/view/react-resizable.css'
import React from 'react';
import { Layout,PageHeader,Row,Col} from 'antd'
import { Resizable } from 'react-resizable';
import Main from './Main/MIndex'
import Side from './Sider/SIndex';
import Bus from '../../util/events'

const { Content, Sider } = Layout;

export default class Design extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
            siderLeftCollapsed: false,
            siderLeftWidth: 250,
        }
    }

    handleSiderState(state){
        this.setState({siderLeftCollapsed:state})
    }

    componentDidMount() {
        let container = document.querySelector("."+style["wch-container"]);
        Bus.emit("containerRect",container.offsetWidth,container.offsetHeight)
    }

    render() {
        return (
            <Layout className={style["wch-container"]}>
                <PageHeader
                    className={style["wch-head"]}
                    title={
                        <div>
                            <cn>自定义布局管理器</cn>
                            <en>Custom Layout Manager</en>
                        </div>
                    }
                >
                </PageHeader>
                <Layout className={style["wch-layout-inside"]}>
                    <Resizable axis="x"
                               minConstraints={[250,0]}
                               maxConstraints={[250,0]}
                               width={this.state.siderLeftWidth}
                               height={0}
                               onResize={(event, { element, size }) => {
                                   this.setState({
                                       siderLeftWidth: size.width,
                                   });
                               }}>
                        <Sider className={[`${style["wch-sider-left"]}`,`${style["wch-box-shadow"]}`].join(' ')}
                               width={this.state.siderLeftWidth}
                               theme={'light'}
                               trigger={null}
                               collapsedWidth={20}
                               collapsible collapsed={this.state.siderLeftCollapsed}>
                            <Side callback={this.handleSiderState.bind(this)}></Side>
                        </Sider>
                    </Resizable>
                    <Content
                        className={[`${style["wch-content"]}`,`${style["wch-box-shadow"]}`].join(' ')}>
                        <Main designSideLeftWidth={this.state.siderLeftWidth}/>
                    </Content>

                </Layout>
            </Layout>
        );
    }
}