
const defaultState = {
    defLays: [],
    activeId:-1,
    tempLays: [],
    configs:[]
}

export default (state = defaultState, action) => {
    if (action.type === 'updateDefLays') {
        let repos = JSON.parse(JSON.stringify(state));
        repos.defLays = JSON.parse(JSON.stringify(action.value));
        return repos;
    }
    if (action.type === 'updateActiveId') {
        let repos = JSON.parse(JSON.stringify(state));
        repos.activeId = action.value;
        return repos;
    }
    if (action.type === 'updateTempLays') {
        let repos = JSON.parse(JSON.stringify(state));
        repos.tempLays = JSON.parse(JSON.stringify(action.value));
        return repos;
    }
    if (action.type === 'updateConfigs') {
        let repos = JSON.parse(JSON.stringify(state));
        repos.configs = JSON.parse(JSON.stringify(action.value));
        return repos;
    }
    return state;
}