import style from '../../../css/view/mindex.module.less'
import React from 'react';
import { DragDropContext } from 'react-dnd';
import HTMLBackend from 'react-dnd-html5-backend';
import Touch from './Touch';
import Variety from './Variety'
import  Setting from './Setting';
import Equ from './Equilizer'
import axios from 'axios';
import hostIp from "../../../util/host";
import { Resizable } from 'react-resizable';
import AddModal from "./AddModal"
import { CheckSquareFilled,StarFilled,ReloadOutlined } from "@ant-design/icons"
import Bus from '../../../util/events'
import rpc from '../../../util/rpc'

import { Layout, Row, Col, Divider,Button,Popover,Checkbox,Modal } from 'antd'
const { Content, Sider } = Layout;

@DragDropContext(HTMLBackend)
class MIndex extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
            siderLeftWidth: 119, //63
            contentWidth: 0,
            siderRightWidth: 300,
            configs: null,
            activeAudio:[]
        }
    }

    setChecked =()=> {
        //与SIndex.js组件通信
        Bus.emit("witchLay","enable");
    }

    setDefault =()=>{
        //与SIndex.js组件通信
        Bus.emit("witchLay","default");
    }

    onCheckboxChange =vals=>{
        this.setState({
            activeAudio: vals
        })
    }

    onSetAudioCfg =()=>{
        let {configs,activeAudio} = this.state;
        for(let i=0;i<configs.length;i++){
            let cfg = configs[i];
            if(cfg.type === "mix"){
                cfg["srcA"] = activeAudio;
            }
        }
        rpc("enc.update",[JSON.stringify(configs,null,2)],result=>{
            Modal.success({
                title: '提示',
                centered: true,
                content: '设置成功',
            });
        })
    }

    async queryData(url){
        // 同步请求
        let resultData = [];
        let dataUrl = 'http://'+hostIp()+'/'+url;
        try {
            let res = await axios({
                url: dataUrl,
                method: 'get',
            });

            if (res.status === 200) { // 查询到结果，返回数据
                resultData = res.data;
            }
        } catch (err) {
            console.log(err);
        }

        return resultData;
    }

    async componentDidMount() {

        let configs = await this.queryData("config/config.json");
        this.setState({"configs":configs})

        let ele = document.querySelector("." + style["wch-content-inside"]);
        this.setState({
            contentWidth: ele.offsetWidth
        });
    }

    render() {

        const {configs} = this.state;
        let content;
        if(configs !== null){
            let srcA = [];
            for(let i=0;i<configs.length;i++){
                let config = configs[i];
                if(config.type == "mix"){
                    srcA = config["srcA"];
                }
            }
            content = (
                <Row>
                    <Col>
                        <Checkbox.Group onChange={this.onCheckboxChange} defaultValue={srcA}>
                            <Row>
                                {
                                    configs.map((cfg,index)=>{
                                        //if(cfg.enable)
                                        return (
                                            <Col span={6} key={index}>
                                                <Checkbox style={{marginTop:"15px"}} value={cfg.id+""}>{cfg.name}</Checkbox>
                                            </Col>
                                        )
                                    })
                                }
                            </Row>
                        </Checkbox.Group>
                        <Divider />
                        <Row>
                            <Col span={6} offset={18}>
                                <Button type="primary" onClick={this.onSetAudioCfg}>设定</Button>
                            </Col>
                        </Row>
                    </Col>
                </Row>

            );
        }

        return (
            <Layout className={style["wch-container"]}>
                <Content className={style["wch-content-box"]}>
                    <Row className={style["wch-row1-box"]} align={"middle"}>
                        {/*<Col span={16}>*/}
                            {/*<Row>*/}
                                {/*<Col span={2} onClick={this.setChecked}>*/}
                                    {/*<Row>*/}
                                        {/*<Col span={5} offset={4}>*/}
                                            {/*<StarFilled  className={style["wch-handle-btn"]}/>*/}
                                        {/*</Col>*/}
                                        {/*<Col span={15} className={style["wch-handle-btn"]} style={{borderRight:"1px solid gray"}}>*/}
                                            {/*<span >*/}
                                                {/*启用*/}
                                            {/*</span>*/}
                                        {/*</Col>*/}
                                    {/*</Row>*/}
                                {/*</Col>*/}
                                {/*<Col span={2} onClick={this.setDefault}>*/}
                                    {/*<Row>*/}
                                        {/*<Col span={5} offset={4}>*/}
                                            {/*<ReloadOutlined  className={style["wch-handle-btn"]}/>*/}
                                        {/*</Col>*/}
                                        {/*<Col span={15} className={style["wch-handle-btn"]} style={{borderRight:"1px solid gray"}}>*/}
                                    {/*<span >*/}
                                        {/*还原*/}
                                    {/*</span>*/}
                                        {/*</Col>*/}
                                    {/*</Row>*/}
                                {/*</Col>*/}
                            {/*</Row>*/}
                        {/*</Col>*/}
                    </Row>

                    <Row className={[`${style["wch-row2-box"]}`,`${style["wch-box-shadow"]}`].join(' ')}>
                        <Col className={style["wch-col2-box"]} span={24}>
                            <Layout className={style["wch-layout-inside"]}>
                                <Resizable axis="x"
                                           minConstraints={[119,0]}
                                           maxConstraints={[119,0]}
                                           width={this.state.siderLeftWidth}
                                           height={0}
                                           onResize={(event, { element, size }) => {
                                               this.setState({
                                                   siderLeftWidth: size.width,
                                               });
                                           }}>
                                    <Sider theme={'light'}
                                           className={style["wch-sider-left"]}
                                           width={this.state.siderLeftWidth}
                                    >
                                        <Row>
                                            <Col>
                                                <Row>
                                                    {this.state.configs ? this.state.configs.map((config,index)=> {
                                                        let name = config.name;
                                                        name = name.replace(/\s*/g,"");
                                                        if(name.length >= 6);
                                                            name = name.substring(0,6);

                                                        let content;
                                                        if(config.type !== "file")
                                                            content = (
                                                                <Variety name={name} id={config.id} enable={config.enable} key={index}/>
                                                            )

                                                        return content;
                                                    }) : null}
                                                    <Variety name={"/"} id={-1} key={999}/>
                                                </Row>
                                                {/*<Divider className={style["wch-divider"]}/>*/}
                                                {/*<Row>*/}
                                                    {/*<Variety name={"layout"} />*/}
                                                {/*</Row>*/}
                                            </Col>
                                        </Row>
                                    </Sider>
                                </Resizable>

                                <Resizable axis="x"
                                           width={this.state.contentWidth}
                                           height={0}
                                           onResize={(event, { element, size }) => {
                                               let diff = size.width - this.state.contentWidth;
                                               let siderRightWidth = this.state.siderRightWidth-diff;
                                               if(siderRightWidth <= 200)
                                                   return;
                                               this.setState({
                                                   contentWidth: size.width,
                                                   siderRightWidth: siderRightWidth
                                               });
                                           }}>
                                    <Content className={style["wch-content-inside"]}>
                                        <div className={style["wch-touch-box"]}>
                                            <Touch designSideLeftWidth={this.props.designSideLeftWidth}
                                                   mainSideLeftWidth={this.state.siderLeftWidth}
                                                   mainSiderRightWidth={this.state.siderRightWidth}>
                                            </Touch>
                                        </div>
                                        {/*<Popover placement="top" content={content} title="设定音频源" trigger="hover">*/}
                                            {/*<div className={style["wch-equilizer-box"]}>*/}
                                                {/*<Equ />*/}
                                            {/*</div>*/}
                                        {/*</Popover>*/}
                                    </Content>
                                </Resizable>

                                <Sider  width={this.state.siderRightWidth}
                                        className={[`${style["wch-sider-right"]}`,`${style["wch-box-shadow"]}`].join(' ')}
                                        theme={'light'}>
                                    <Setting siderRightWidth={this.state.siderRightWidth}/>
                                </Sider>
                            </Layout>
                        </Col>
                    </Row>
                    <Row className={style["wch-row3-box"]}></Row>
                    <AddModal></AddModal>
                </Content>
            </Layout>
        );
    }
}
export default MIndex;