import style from '../../../css/view/nature.module.less'
import React from "react";
import { Table,InputNumber } from "antd";
import { Resizable } from 'react-resizable';
import Bus from "../../../util/events"

const ResizeableTitle = props => {
    const { onResize, width, ...restProps } = props;

    if (!width) {
        return <th {...restProps} />;
    }

    return (
        <Resizable
            width={width}
            height={0}
            handle={
                <span className={[`${style["react-resizable-handle"]}`,`${style["react-resizable-handle-ew"]}`].join(' ')}></span>
            }
            onResize={onResize}
            draggableOpts={{ enableUserSelectHack: false }}
        >
            <th {...restProps} />
        </Resizable>
    );
};

export default class Nature extends React.Component {
    state = {
        columns: [
            {
                title: ()=>{
                    return (
                        <div>
                            <cn>名称</cn>
                            <en>Name</en>
                        </div>
                    )
                },
                dataIndex: 'name',
                width: 60,
                render:(text,record,index) => {
                    if(record.mark === "name"){
                        return (
                            <div>
                                <cn>名称</cn>
                                <en>Name</en>
                            </div>
                        )
                    }
                    if(record.mark === "touchW"){
                        return (
                            <div>
                                <cn>宽度</cn>
                                <en>Width</en>
                            </div>
                        )
                    }
                    if(record.mark === "touchH"){
                        return (
                            <div>
                                <cn>高度</cn>
                                <en>Height</en>
                            </div>
                        )
                    }
                    if(record.mark === "display"){
                        return (
                            <div>
                                <cn>分辨率</cn>
                                <en>Display</en>
                            </div>
                        )
                    }
                    if(record.mark === "posX"){
                        return (
                            <div>
                                <cn>左(X)</cn>
                                <en>Left(X)</en>
                            </div>
                        )
                    }
                    if(record.mark === "posY"){
                        return (
                            <div>
                                <cn>上(Y)</cn>
                                <en>Top(Y)</en>
                            </div>
                        )
                    }
                    if(record.mark === "width"){
                        return (
                            <div>
                                <div>
                                    <cn>宽度</cn>
                                    <en>Width</en>
                                </div>
                            </div>
                        )
                    }
                    if(record.mark === "height"){
                        return (
                            <div>
                                <cn>高度</cn>
                                <en>Height</en>
                            </div>
                        )
                    }

                }
            },
            {
                title: ()=>{
                    return (
                        <div>
                            <cn>属性</cn>
                            <en>Option</en>
                        </div>
                    )
                },
                dataIndex: 'value',
                width: 100,
                editable:true,
                render:(text,record,index)=>{

                    if(text < 0)
                        text = 0;

                    if(
                        record.mark === "name" ||
                        record.mark === "type" ||
                        record.mark === "display" ||
                        record.mark === "touchW" ||
                        record.mark === "touchH"
                    )
                        return text;

                    return (
                        <InputNumber className={index%2 == 0 ? style["inputNum"] : [`${style["inputNum"]}`,`${style["inputodd"]}`].join(' ')} size={"small"} value={text}
                                     onStep={
                                         val =>{
                                             this.onStep(val,record.mark);
                                         }
                                     }
                                     onChange={
                                         (val)=>{
                                             this.setState({
                                                 lastVal: val,
                                                 lastType : record.mark
                                             })
                                         }
                                     }
                                     onBlur={
                                         ()=>{
                                             if(this.state.lastVal === -1 || this.state.lastType === "")
                                                 return;
                                             this.onStep(this.state.lastVal,this.state.lastType);
                                         }
                                     }
                        />
                    )
                }
            }
        ],
        data:[],
        data1: [
            {
                key: 0,
                name: '名称',
                value: 'container',
                mark: 'name',
            },
            // {
            //     key: 1,
            //     name: '类型',
            //     value: '容器',
            //     mark: 'type',
            // },
            {
                key: 2,
                name: '宽度',
                value: '1920',
                mark: 'touchW',
            },
            {
                key: 3,
                name: '高度',
                value: '1080',
                mark: 'touchH',
            },
            {
                key: 4,
                name: '分辨率',
                value: '1920 x 1080',
                mark: 'display'
            },
        ],
        data2: [
            {
                key: 0,
                name: '名称',
                value: '-',
                mark: 'name',
            },
            // {
            //     key: 1,
            //     name: '类型',
            //     value: '-',
            //     mark: 'type',
            // },
            {
                key: 2,
                name: '左(X)',
                value: '-',
                mark: 'posX',
            },
            {
                key: 3,
                name: '上(Y)',
                value: '-',
                mark: 'posY',
            },
            {
                key: 4,
                name: '宽度',
                value: '-',
                mark: 'width',
            },
            {
                key: 5,
                name: '高度',
                value: '-',
                mark: 'height'
            },
        ],
        lastVal:-1,
        lastType:"",
    };

    components = {
        header: {
            cell: ResizeableTitle,
        },
    };


    handleResize = index => (e, { size }) => {
        this.setState(({ columns }) => {
            const nextColumns = [...columns];
            nextColumns[index] = {
                ...nextColumns[index],
                width: size.width,
            };
            return { columns: nextColumns };
        });
    };


    onStep = (val,type) => {

        let data = this.state.data;
        for(let i=0;i<data.length;i++) {
            let dd = data[i];
            if(dd.mark === type) {
                dd.value = val;
                data[i] = dd;
            }
        }
        this.setState({data:data});

        // 与touch.js通信，改变滑块大小
        Bus.emit("changeRndInfo",type,val)
    }

    componentDidMount(){
        // 接收Touch.js组件消息
        this.setState({data:this.state.data1})
        this.eventEmitter = Bus.addListener('showNature', (nature,index) => {
            let data;
            if(index >= 0){
                data = this.state.data2;
                for(let i=0;i<data.length;i++){
                    if(data[i].mark === "name")
                        data[i].value = nature.name;
                    if(data[i].mark === "type")
                        data[i].value = nature.mark;
                    if(data[i].mark === "posX")
                        data[i].value = 2 > Math.round(nature.pos.x * 1920) ? 0 : Math.round(nature.pos.x * 1920);
                    if(data[i].mark === "posY")
                        data[i].value = 2 > Math.round(nature.pos.y * 1080) ? 0 : Math.round(nature.pos.y * 1080);
                    if(data[i].mark === "width")
                        data[i].value = 1920 > Math.round(nature.pos.w * 1920) ? Math.round(nature.pos.w * 1920) :1920;
                    if(data[i].mark === "height")
                        data[i].value = 1080 > Math.round(nature.pos.h * 1080) ? Math.round(nature.pos.h * 1080) : 1080;
                }
            } else {
                data = this.state.data1;
                for(let i=0;i<data.length;i++){
                    if(data[i].mark === "name")
                        data[i].value = nature.name;
                    if(data[i].mark === "type")
                        data[i].value = nature.mark;
                    if(data[i].mark === "touchW")
                        data[i].value = Math.round(nature.width);
                    if(data[i].mark === "touchH")
                        data[i].value = Math.round(nature.height);
                    if(data[i].mark === "display")
                        data[i].value = nature.display;
                }
            }
            this.setState({data:data})
        });
    }

    render() {
        const columns = this.state.columns.map((col, index) => ({
            ...col,
            onHeaderCell: column => ({
                width: column.width,
                onResize: this.handleResize(index),
            }),
        }));

        return <Table
            className={style.table}
            bordered
            pagination={false}
            size={'small'}
            rowClassName={(record, index) => { return index % 2 === 1?style.odd:style.enev }}
            components={this.components}
            columns={columns}
            dataSource={this.state.data} />;
    }
}