import React from 'react';
import PropTypes from 'prop-types';
import { DragSource } from 'react-dnd';
import Marks from './Marks'


const style = {
	border: 'none',
	//backgroundColor: '#666',
    backgroundColor: '#555',
	color: 'white',
	cursor: 'move',
	height: '50px',
	width: '50px',
	textAlign: 'center',
	fontSize: '25px',
	lineHeight: '50px',
	marginTop: '10px',
	marginLeft: '5px'
}

const boxSource = {
	/**
	 * 开始拖拽时触发当前函数
	 * @param {*} props 组件的 props
	 */
	beginDrag(props) {
		// 返回的对象可以在 monitor.getItem() 中获取到
		let id = props.id;
		if(id === -1)
			id = 0-Math.round(Math.random()*100000);
		return {
            id:	id,
			name: props.name,
			enable: props.enable

		}
	},

	// /**
	//  * 拖拽结束时触发当前函数
	//  * @param {*} props 当前组件的 props
	//  * @param {*} monitor DragSourceMonitor 对象
	//  */
	// endDrag(props, monitor) {
	// 	// 当前拖拽的 item 组件
	// 	//const item = monitor.getItem();
	// 	// 拖拽元素放下时，drop 结果
	// 	const dropResult = monitor.getDropResult();
    //
	// 	// 如果 drop 结果存在，就弹出 alert 提示
	// 	//if (dropResult) {
	// 		//alert(`You dropped ${item.name} into ${dropResult.name}!`);
	// 	//}
	// },
}

@DragSource(
	// type 标识，这里是字符串 'box'
	//ItemTypes.BOX,
	'box',
	// 拖拽事件对象
	boxSource,
	// 收集功能函数，包含 connect 和 monitor 参数
	// connect 里面的函数用来将 DOM 节点与 react-dnd 的 backend 建立联系
	(connect, monitor) => ({
		// 包裹住 DOM 节点，使其可以进行拖拽操作
		connectDragSource: connect.dragSource(),
		// 是否处于拖拽状态
		isDragging: monitor.isDragging()
	}),
)
class Variety extends React.Component {

	static propTypes = {
		name: PropTypes.string.isRequired,
		isDragging: PropTypes.bool.isRequired,
		connectDragSource: PropTypes.func.isRequired
	}


	render() {
		const { isDragging, connectDragSource } = this.props
		const opacity = isDragging ? 0.4 : 1
		// 使用 connectDragSource 包裹住 DOM 节点，使其可以接受各种拖动 API
		// connectDragSource 包裹住的 DOM 节点才可以被拖动
		return connectDragSource && connectDragSource(
				<div style={{ ...style, opacity }}>
					<Marks name={this.props.name} />
				</div>
			);
	}
}

export default Variety;