import style from '../../../css/view/sindex.module.less'
import React from "react";
import { Layout, Row, Col, Modal } from "antd";
import { CopyFilled, SaveFilled, DeleteFilled,ExclamationCircleOutlined,CheckOutlined,StarFilled } from "@ant-design/icons"
import Bus from '../../../util/events'
import hostIp from "../../../util/host"
import axios from "axios/index"
import func from '../../../util/func'
import rpc from "../../../util/rpc"
import { connect } from "react-redux"
import store from "../../../store/index"
import cookie from "react-cookies"

const { Header, Content } = Layout;
const { confirm } = Modal;

class SIndex extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            siderLeftCollapsed: false,
        }
    }

    // handleArrow = (state) =>{
    //     this.setState({siderLeftCollapsed:!state})
    //     this.props.callback(!state);
    //     // 与Touch.js组件通信
    //     Bus.emit('changeCollapsed', !state);
    // }

    addLayout = () =>{

        let count = this.props.tempLays.length;
        let lang = cookie.load("lang");
        let title = "提示";
        let content = "最多保存10个布局，请删除其他布局后在添加";
        if(lang === "en"){
            title = "Tip";
            content = "Save up to 10 layouts. Delete other layouts before adding";
        }
        if(count >= 10){
            Modal.warning({
                title: title,
                centered: true,
                content: content,
            });
            return;
        }
        // 与AddModal.js组件通信
        Bus.emit("showAddModal");
    }

    saveLayout = () =>{
        let rndArray = new Array();
        let tempLay = this.props.tempLays[this.props.activeId];
        let enable = tempLay.enable;
        let trnds = tempLay.layouts;
        let mixLay = [];
        for(let i=0;i<trnds.length;i++) {
            let srnd = trnds[i];
            let key = srnd.key;
            if(key === undefined || key === "")
                key = Math.round(Math.random()*100000);
            let trnd = {
                id: srnd.id,
                name: srnd.name,
                lock:true,
                key: key,
                //click: false,
                //type: srnd.type,
                //color: srnd.color,
                //mark:srnd.mark,
                //enableDrag: true,
                pos:{
                    a: srnd.pos.a,
                    x: parseFloat(parseFloat(srnd.pos.x).toFixed(4)),
                    y: parseFloat(parseFloat(srnd.pos.y).toFixed(4)),
                    w: parseFloat(parseFloat(srnd.pos.w).toFixed(4)),
                    h: parseFloat(parseFloat(srnd.pos.h).toFixed(4)),
                    index:srnd.pos.index,
                }
            }
            rndArray.push(trnd);
            mixLay.push(trnd.pos);
        }

        let obj = {
            layId:this.props.activeId,
            layName:this.props.defLays[this.props.activeId].layName,
            enable:false,
            layouts:rndArray
        }
        let layList = this.props.defLays;
        let index = -1;
        for(let i=0;i<layList.length;i++){
            let lay = layList[i];
            lay.enable = false;
            if(obj.layId === lay.layId)
                index = i;

            let rnds = lay.layouts;
            for(let i=0;i<rnds.length;i++){
                let rnd = rnds[i];
                if(rnd.key === undefined || rnd.key === ""){
                    rnd.key = Math.round(Math.random()*100000);
                }
            }
        }
        if(index > -1) {
            obj.enable = layList[index].enable;
            if(layList[index].enable === undefined || layList[index].enable === "")
                obj.enable = false;
            layList[index] = obj;
        } else {
            layList.push(obj);
        }

        func("saveConfigFile",{path: "/config/defLays.json",data: JSON.stringify(layList,null,2)},(ret)=>{
            if(ret.result === "OK"){
                this.props.updateLayout(layList);
                let lang = cookie.load("lang");
                let title = "提示";
                let content = "保存成功";
                if(lang === "en"){
                    title = "Tip";
                    content = "Save success";
                }
                Modal.warning({
                    title: title,
                    centered: true,
                    content: content,
                });
            }
        })

        if(enable){
            let configs = this.props.configs;
            for(let i=0;i<configs.length;i++){
                let conf = configs[i];
                if(conf.type === "mix"){
                    let conf = configs[i];
                    conf.layout = mixLay;
                }
            }
            this.props.updateConfigs(configs);
            rpc("enc.update",[JSON.stringify(configs,null,2)],result=>{
                // Modal.success({
                //     title: '提示',
                //     centered: true,
                //     content: '设置成功',
                // });
            })
        }
    }
    delLayout = async () =>{
        let defLays = this.props.defLays;
        let layName,layNameEn;
        for(let i=0;i<defLays.length;i++) {
            if(defLays[i].layId === this.props.activeId){
                layName = defLays[i].layName;
                layNameEn = defLays[i].layNameEn;
            }

        }

        let lang = cookie.load("lang");
        let title = "提示";
        let content = "是否要删除布局：" + layName +"?";
        let okText = "确定";
        let cancelText = "取消";
        if(lang === "en"){
            title = "Tip";
            content = "Whether to delete the layout of " + layNameEn + "?";
            okText = "OK";
            cancelText = "Cancel";
        }

        let handle = this;
        confirm({
            title: title,
            icon: <ExclamationCircleOutlined />,
            content: content,
            okText: okText,
            okType: 'primary',
            cancelText: cancelText,
            centered: true,
            async onOk() {
                let list = new Array();
                for(let i=0;i<defLays.length;i++) {
                    if(defLays[i].layId !== handle.props.activeId)
                        list.push(defLays[i]);
                }
                for(let i=0;i<list.length;i++){
                    list[i].layId = i;
                }
                func("saveConfigFile",{path: "/config/defLays.json",data: JSON.stringify(list,null,2)},(ret)=>{
                    if(ret.result === "OK"){
                        handle.props.updateLayout(list);
                        handle.props.updateTempLays(list);
                        handle.props.updateActiveId(0);
                    }
                })
            },
            onCancel() {
                //console.log('Cancel');
            },
        });
    }

    async queryData(url){
        // 同步请求
        let resultData = [];
        let dataUrl = 'http://'+hostIp()+'/'+url;
        try {
            let res = await axios({
                url: dataUrl,
                method: 'get',
            });

            if (res.status === 200) { // 查询到结果，返回数据
                resultData = res.data;
            }
        } catch (err) {
            console.log(err);
        }

        return resultData;
    }

    async componentDidMount(){
        let defLays = await this.queryData("config/defLays.json");
        let configs = await this.queryData("config/config.json");

        let mixV = [],sysLay = [];
        for(let i=0;i<defLays.length;i++){
            let layouts = defLays[i].layouts;
            let temp = [];
            for(let j=0;j<layouts.length;j++){
                let lay = layouts[j];
                temp.push(lay.pos);
                if(lay.id < 0){
                    lay.enable = false;
                    continue;
                }
                for(let k =0;k<configs.length;k++){
                    let conf = configs[k];
                    if(conf.type === "mix")
                        mixV = conf["layout"];
                    if(lay.id+"" === conf.id+""){
                        lay.enable = conf.enable;
                    }
                }
            }
            sysLay.push(temp);
        }

        for(let i=0;i<sysLay.length;i++){
            if(JSON.stringify(mixV) === JSON.stringify(sysLay[i]))
                defLays[i].enable = true;
        }

        this.props.updateLayout(defLays);
        this.props.updateTempLays(defLays);
        this.props.updateConfigs(configs);

        setTimeout(()=>{
            this.props.updateActiveId(0);
        },200)


        // 接收AddModal.js的消息
        Bus.addListener("addLayName",async (layName)=>{
            let defLays = this.props.defLays;
            for(let i=0;i<defLays.length;i++){
                defLays[i].layId = i;
            }
            defLays.push({
                layId:defLays.length,
                layName:layName,
                layNameEn:layName,
                layouts:[]
            });
            func("saveConfigFile",{path: "/config/defLays.json",data: JSON.stringify(defLays,null,2)},(ret)=>{
                if(ret.result === "OK"){
                    this.props.updateLayout(defLays);
                }
            })
        })

        //接收MIndex.js组件消息
        // Bus.addListener("witchLay",async (type)=>{
        //     const { layConfig,layoutClickId } = this.state;
        //     let configs = await this.queryData("config/config.json");
        //     let defLays = await this.queryData("config/defLays.json");
        //
        //     for(let i=0;i<defLays.length;i++){
        //         let lay = defLays[i];
        //         if(lay.layId === layoutClickId)
        //             lay[type] = true;
        //         else
        //             lay[type] = false;
        //     }
        //
        //     func("saveConfigFile",{path: "/config/defLays.json",data: JSON.stringify(defLays,null,2)},(ret)=>{
        //         if(ret.result === "OK"){
        //             this.setState({
        //                 layConfig:defLays
        //             })
        //
        //             if(type === "enable"){
        //                 let lays;
        //                 for(let i=0;i<defLays.length;i++){
        //                     if(defLays[i].layId === layoutClickId)
        //                         lays = defLays[i].layouts;
        //                 }
        //
        //                 let srcV = [],layouts = [];
        //                 for(let i=0;i<lays.length;i++){
        //                     let lay = lays[i];
        //                     srcV.push(lay.id);
        //                     let obj = {
        //                         "x": parseFloat(lay.posX/lay.touchW),
        //                         "y": parseFloat(lay.posY/lay.touchH),
        //                         "w": parseFloat(lay.width/lay.touchW),
        //                         "h": parseFloat(lay.height/lay.touchH),
        //                         "a": 1,
        //                         "index": lay.index
        //                     }
        //                     layouts.push(obj);
        //                 }
        //
        //                 for(let i=0;i<configs.length;i++){
        //                     let cfg = configs[i];
        //                     if(cfg.type === "mix"){
        //                         cfg["srcV"] = srcV;
        //                         cfg["layout"] = layouts;
        //                     }
        //                 }
        //
        //                 rpc("enc.update",[JSON.stringify(configs,null,2)],result=>{
        //                     // Modal.success({
        //                     //     title: '提示',
        //                     //     centered: true,
        //                     //     content: '设置成功',
        //                     // });
        //                 })
        //             }
        //         }
        //     })
        // })
    }

    render() {
        return(
            <Layout className={style["wch-container"]}>
                <Header className={
                    !this.state.siderLeftCollapsed ? style["wch-header-wrap"] : [`${style["wch-header-wrap"]}`,`${style["active"]}`].join(' ')
                }>
                    <Row className={style["wch-row-box"]} align={"middle"}>
                        <Col span={8} onClick={this.addLayout}>
                            <Row>
                                <Col span={6} offset={4}>
                                    <CopyFilled className={!this.state.siderLeftCollapsed ?  style["wch-handle-btn"] : [`${style["wch-handle-btn"]}`,`${style["hide"]}`].join(' ')}/>
                                </Col>
                                <Col span={14} style={{borderRight:"1px solid gray"}}>
                                    <span className={!this.state.siderLeftCollapsed ?  style["wch-handle-btn"] : [`${style["wch-handle-btn"]}`,`${style["hide"]}`].join(' ')}>
                                        <cn>新建</cn>
                                        <en>New</en>
                                    </span>
                                </Col>
                            </Row>
                        </Col>
                        <Col span={8} onClick={this.saveLayout}>
                            <Row>
                                <Col span={6} offset={4}>
                                    <SaveFilled className={!this.state.siderLeftCollapsed ?  style["wch-handle-btn"] : [`${style["wch-handle-btn"]}`,`${style["hide"]}`].join(' ')}/>
                                </Col>
                                <Col span={14} style={{borderRight:"1px solid gray"}}>
                                    <span className={!this.state.siderLeftCollapsed ?  style["wch-handle-btn"] : [`${style["wch-handle-btn"]}`,`${style["hide"]}`].join(' ')}>
                                        <cn>保存</cn>
                                        <en>Save</en>
                                    </span>
                                </Col>
                            </Row>
                        </Col>
                        <Col span={8} onClick={this.delLayout}>
                            <Row>
                                <Col span={6} offset={4}>
                                    <DeleteFilled className={!this.state.siderLeftCollapsed ?  style["wch-handle-btn"] : [`${style["wch-handle-btn"]}`,`${style["hide"]}`].join(' ')}/>
                                </Col>
                                <Col span={14}>
                                    <span className={!this.state.siderLeftCollapsed ?  style["wch-handle-btn"] : [`${style["wch-handle-btn"]}`,`${style["hide"]}`].join(' ')}>
                                        <cn>删除</cn>
                                        <en>Delete</en>
                                    </span>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                </Header>
                <Content
                    className={
                        !this.state.siderLeftCollapsed ? style["wch-content-wrap"] : [`${style["wch-content-wrap"]}`,`${style["active"]}`].join(' ')
                    }>

                    {this.props.defLays.map((lay,index)=>{
                        return (
                                <Row key={index}
                                    className={
                                    this.props.activeId !== lay.layId ? style["wch-wrap-item"] : [`${style["wch-wrap-item"]}`,`${style["active"]}`].join(' ')
                                }
                                     onClick={
                                         ()=>{
                                             this.props.updateActiveId(lay.layId);
                                         }
                                     }>
                                    <Col span={16} offset={4}>
                                        <span>
                                            <cn>{lay.layName}</cn>
                                            <en>{lay.layNameEn}</en>
                                        </span>
                                    </Col>
                                    <Col span={2} offset={2}>
                                        {lay.enable ? <StarFilled /> : null}
                                    </Col>
                                </Row>
                            )
                    })}
                </Content>
            </Layout>
        )
    }
}

const stateToProps = (state) => {
    return {
        defLays: state.defLays,
        activeId: state.activeId,
        tempLays: state.tempLays,
        configs: state.configs
    }
}

const dispatchTOProps = (dispatch) => {
    return {
        updateLayout(lays) {
            let action = {
                type: 'updateDefLays',
                value: lays
            }
            dispatch(action)
        },
        updateActiveId(activeId){
            let action = {
                type: 'updateActiveId',
                value: activeId
            }
            dispatch(action)
        },
        updateTempLays(lays) {
            let action = {
                type: 'updateTempLays',
                value: lays
            }
            dispatch(action)
        },
        updateConfigs(confs) {
            let action = {
                type: 'updateConfigs',
                value: confs
            }
            dispatch(action)
        }
    }
}

export default connect(stateToProps, dispatchTOProps)(SIndex);