import style from '../../../css/view/touch.module.less'
import React from 'react';
import PropTypes from 'prop-types';
import { DropTarget } from 'react-dnd';
import { Rnd } from 'react-rnd'
import Menu from './Menu'
import Layout from "./Layout";
import Bus from '../../../util/events'
import Line from "./Line";
import Place from "./Place"
import { Row, Col, Modal } from 'antd'
import func from '../../../util/func'
import hostIp from "../../../util/host"
import axios from "axios/index"
import { connect } from "react-redux"
import Grid8x8 from "../Grid/Grid8x8"
import Grid3x3 from "../Grid/Grid3x3"
import Grid2x2 from "../Grid/Grid2x2"
import Grid2Split from "../Grid/Grid2Split"
import Grid1And2 from "../Grid/Grid1And2"
import Grid2And1 from "../Grid/Grid2And1"
import Grid1And3 from "../Grid/Grid1And3"
import Grid3And1 from "../Grid/Grid3And1"


const boxTarget = {
	// 当有对应的 drag source 放在当前组件区域时，会返回一个对象，可以在 monitor.getDropResult() 中获取到
	//drop: () => ({ name: 'Dustbin' })
	drop: (props,monitor,component) => {

        const hasDroppedOnChild = monitor.didDrop();
        if (hasDroppedOnChild && !props.greedy) {
            return;
        }
		const pos = monitor.getClientOffset();
		const item = monitor.getItem();

        let rnds = component.state.rnds;
        for(let i=0;i<rnds.length;i++){
            if(item.name === rnds[i].name && item.name !== "/"){
                Modal.warning({
                    title: '提示',
                    centered: true,
                    content: '模块已经存在',
                });
                return;
            }
        }


		const width = component.state.touchWidth * 0.25;
		const height = component.state.touchHeight *0.25;

		// 空间左上角坐标
		let posX = pos.x - component.state.touchX;
		let posY = pos.y - component.state.touchY-70;

		// 把控件中心移动到鼠标位置
		let x = posX - width/2;
		let y = posY - height/2;

		// 容器边缘检测
		if(posX - width/2 < 0)
			x = 0;

		if(posX + width/2 > component.state.touchWidth)
			x = component.state.touchWidth - width;

        if(posY - height/2 < 0)
            y = 0;

		if(posY + height/2 > component.state.touchHeight)
			y = component.state.touchHeight - height;


		let rnd = {
			id: item.id,
			name: item.name,
            enable: item.enable,
			key: Math.round(Math.random()*100000),
            //type: item.name,
            // color: 'lightgray',
            // click: false,
            // enableDrag: true,
            lock: true,
            pos: {
			    a: 1,
			    x: x/component.state.touchWidth,
                y: y/component.state.touchHeight,
                w: width/component.state.touchWidth,
                h: height/component.state.touchHeight,
                index: rnds.length+1
            }
		}

		//rnd.mark = item.name;

		let tempLays = component.props.tempLays;
		let tempLay = tempLays[component.props.activeId];
		let trnds = tempLay.layouts;
		trnds.push(rnd);
		tempLay.layouts = trnds;
		component.props.updateTempLays(tempLays);
		component.setState({
			rnds: trnds
		})

		return {name: 'Touch'}
	}
}

@DropTarget(
	// type 标识，这里是字符串 'box'
	//ItemTypes.BOX,
	'box',
	// 接收拖拽的事件对象
	boxTarget,
	// 收集功能函数，包含 connect 和 monitor 参数
	// connect 里面的函数用来将 DOM 节点与 react-dnd 的 backend 建立联系
	(connect, monitor) => ({
		// 包裹住 DOM 节点，使其可以接收对应的拖拽组件
		connectDropTarget: connect.dropTarget(),
		// drag source是否在 drop target 区域
		isOver: monitor.isOver(),
		// 是否可以被放置
		canDrop: monitor.canDrop(),
		isDrop: monitor.getDropResult(),
        isOverCurrent: monitor.isOver({ shallow: true }),
	})
)
class Touch extends React.Component {

    static propTypes = {
        canDrop: PropTypes.bool.isRequired,
        isOver: PropTypes.bool.isRequired,
        connectDropTarget: PropTypes.func.isRequired,
        isOverCurrent: PropTypes.bool.isRequired,
		greedy: PropTypes.bool,
    }

    constructor(props) {
    	super(props)
		this.state = {
    		rnds: [], // 滑块集合
			displayW: 1920, // 分辨率宽
			displayH: 1080, // 分辨率高
			touchWidth: 0, //touch模块宽度 px
			touchHeight: 0, //touch模块高度 px
			touchX:0, // touch 模块x坐标
			touchY:0, // touch 模块y坐标
			deltaX: 0, // touch 模块x坐标和 touch模块父容器x差值
			deltaY: 0, //touch 模块y坐标和 touch模块父容器y差值
			designSiderState:false,//记录 design侧边栏收起状态
			click: false,// 记录touch 是否被点击
			rndClick: false,//记录是否有rnd组件被点击
            rndClickIndex: -1,//记录点击的哪一个rnd组件
            bodyWidth:0, //最外层容器宽度
            bodyHeight:0, //最外层容器高度
            gridId:"0",//使用的网格
		}
	}

	onRndClick(id,index,e) {
		//console.log(index)
        e.stopPropagation();
		let rnds = this.state.rnds;
        //let rnds = this.props.tempLays[this.props.activeId].layouts;
    	for(let i=0;i<rnds.length;i++){
    		if(i === index)
                rnds[i].click = true;
    		else
                rnds[i].click = false;
		}
		this.setState({
			rnds:rnds,
			click: false,
			rndClick: true,
            rndClickIndex: index
		})

    	let configs = this.props.configs;
        let array = [];
        for(let i=0;i<configs.length;i++){
    	    let cfg = configs[i];
    	    let mark = false;
    	    if(id >= 0)
            {
                if(cfg.id === id){
                    array.unshift(cfg);
                    continue;
                }
            }
    	    for(let j=0;j<rnds.length;j++){
                let rnd = rnds[j];
                if(cfg.id === rnd.id){
                    mark = true;
                }
            }
            if(!mark)
                array.push(cfg);
        }

        if(id < 0) {
    	    let cfg = {...configs[0]};
    	    cfg.id = id;
    	    cfg.name = "/";
    	    cfg.type = "none";
    	    cfg.enable = false;
            array.unshift(cfg);
        } else {
            let cfg = {...configs[0]};
            cfg.id = 0-Math.round(Math.random()*100000);
            cfg.name = "/";
            cfg.type = "none";
            array.push(cfg);
        }


		// 与Menu.js组件通信
        Bus.emit('showMenu', id);
    	// 与Nature.js组件通信
        Bus.emit('showNature', this.state.rnds[index],index,this.state.touchWidth,this.state.touchHeight,array);
        // 与Pattern.js组件通信
        Bus.emit('showPattern', this.state.rnds[index],index,this.state.touchWidth,this.state.touchHeight,array);
	}

	onTouchClick(e){
        let rnds = this.state.rnds;
        for(let i=0;i<rnds.length;i++){
			rnds[i].click = false;
        }
        this.setState({
            rnds:rnds,
			click: true,
			rndClick: false,
        })

        // 与Menu.js组件通信
        Bus.emit('showMenu', 999);

        let obj = {
        	name: 'container',
			mark: '容器',
			width: this.state.displayW,
			height: this.state.displayH,
			display: this.state.displayW + " x " + this.state.displayH
		}
        // 与Nature.js组件通信
        Bus.emit('showNature', obj, -1);
        // 与Pattern.js组件通信
        Bus.emit('showPattern', obj, -1);
	}

	onHandleLayout(type) {
		if(type){
            let rnds = this.state.rnds;
            for(let i=0;i<rnds.length;i++)
				rnds[i].click = false;
            this.setState({
                rnds:rnds,
                click: false,
                rndClick: false,
            })
		}
	}

	onResizeTouchPos(){
        // 20 侧边栏收起后的宽度
        let designSideLeftWidth;
        if(this.state.designSiderState)
            designSideLeftWidth = 20;
        else
            designSideLeftWidth = this.props.designSideLeftWidth

        let bodyWidth = this.state.bodyWidth;
        let bodyHeight = this.state.bodyHeight;
        let mainSideLeftWidth = this.props.mainSideLeftWidth;
        let mainSiderRightWidth = this.props.mainSiderRightWidth;

        // 32：标题栏高度
        // 0.05：副标题占用main组件的高度百分比
        // 4,6：边框累计宽度
        // 0.947：设计区域占用main组件高度比
        let mainContentWidth = bodyWidth - designSideLeftWidth - mainSideLeftWidth - mainSiderRightWidth-6;
        let mainContentHeight = bodyHeight - 32;

        let touchWidth = this.state.touchWidth;
        let touchHeight = this.state.touchHeight;

		this.setState({
            touchX: designSideLeftWidth+mainSideLeftWidth+(mainContentWidth-touchWidth)/2+4,
        	touchY: (mainContentHeight*0.947 - touchHeight)/2+32+mainContentHeight*0.05,
        	deltaX: (mainContentWidth-touchWidth)/2,
        	deltaY: (mainContentHeight*0.947 - touchHeight)/2-3-4,
		})
	}

    async queryData(url){
        // 同步请求
        let resultData = [];
        let dataUrl = 'http://'+hostIp()+'/'+url;
        try {
            let res = await axios({
                url: dataUrl,
                method: 'get',
            });

            if (res.status === 200) { // 查询到结果，返回数据
                resultData = res.data;
            }
        } catch (err) {
            console.log(err);
        }

        return resultData;
    }

    UNSAFE_componentWillReceiveProps(){
    	this.onResizeTouchPos();
	}

	componentWillReceiveProps(nextProps) {
        if(nextProps.activeId != this.props.activeId){
            let layouts = [];
            let defLays = this.props.defLays;
            for(let i=0;i<defLays.length;i++){
                if(nextProps.activeId === defLays[i].layId)
                    layouts = defLays[i].layouts;
            }
            this.setState({
                rnds:layouts
            })
            this.props.updateTempLays(this.props.defLays);
        }
    }

	componentDidUpdate(prevProps,prevState){
    	if(this.state.deltaX !== prevState.deltaX || this.state.deltaY !== prevState.deltaY)
		{
			let rnds = this.state.rnds;
			for(let i=0;i<rnds.length;i++){
				if(rnds[i].hasOwnProperty('ref')){
					rnds[i].ref.updatePosition({
						x:this.state.deltaX+rnds[i].posX,
						y:this.state.deltaY+rnds[i].posY,
					})
				}
			}

		}
	}

	componentWillUnmount() {
        this.setState = ()=>false;
    }

	async componentDidMount() {

        Bus.addListener("containerRect",(width,height)=>{
            //使用元素属性获取坐标值不准确
            //特用如下方法获取touch元素的坐标
            let bodyWidth = width;
            let designSideLeftWidth = this.props.designSideLeftWidth;
            let mainSideLeftWidth = this.props.mainSideLeftWidth;
            let mainSiderRightWidth = this.props.mainSiderRightWidth;

            // 32：标题栏高度
            // 4,6：边框累计宽度
            // 20/24：touch宽度占用比例
            let mainContentWidth = bodyWidth - designSideLeftWidth - mainSideLeftWidth - mainSiderRightWidth-6;
            this.setState({
                touchWidth: mainContentWidth *20/24,
                touchHeight: (mainContentWidth *20/24) * this.state.displayH/this.state.displayW,
                bodyWidth: width,
                bodyHeight: height
            },()=>{
                this.onResizeTouchPos();
            })
        })

		// 接收SIndex.js组件消息
        Bus.addListener('changeCollapsed', (state) => {
			this.setState({designSiderState:state},()=>{
				this.onResizeTouchPos();
			});
        });
        // 接收Menu.js组件消息
        Bus.addListener('handleMenu', (type,rndId) => {
            let tempLays = this.props.tempLays;
            let tempLay = tempLays[this.props.activeId];
            let trnds = tempLay.layouts;

            switch (type) {
                case 'delete':// 删除
                    let trndList = [];
                    for(let i=0;i<trnds.length;i++){
                        if(trnds[i].id != rndId){
                            trndList.push(trnds[i]);
                        }
                    }
                    tempLays[this.props.activeId].layouts = trndList;
                    this.props.updateTempLays(tempLays);

                    this.setState({
                        rnds:trndList
                    })
                    break;
                case 'up': // 置顶
                    let index = -1,mark = -1;
                    for(let i=0;i<trnds.length;i++){
                        if(trnds[i].pos.index > index)
                            index = trnds[i].pos.index;
                        if(trnds[i].id === rndId) {
                            mark = i;
                        }
                    }
                    index++;
                    trnds[mark].pos.index = index;
                    this.setState({rnds:trnds});
                    tempLay.layouts = trnds;
                    this.props.updateTempLays(tempLays);
                    break;
                case 'down': // 置底
                    for(let i=0;i<trnds.length;i++){
                        if(trnds[i].id === rndId) {
                            let index = trnds[i].pos.index;
                            index = 0;
                            trnds[i].pos.index = index;
                        }
                    }
                    this.setState({rnds:trnds});
                    tempLay.layouts = trnds;
                    this.props.updateTempLays(tempLays);
                    break;
                case 'align': // 快速对齐
                    let ii = -1;
                    let trnd;
                    for(let i=0;i<trnds.length;i++){
                        if(trnds[i].id === rndId) {
                            trnd = {...trnds[i]};
                            ii = i;
                        }
                    }
                    trnd.enableDrag = !trnd.enableDrag;
                    trnds[ii] = trnd;
                    this.setState({rnds:trnds});
                    tempLay.layouts = trnds;
                    this.props.updateTempLays(tempLays);
                    break;
                case 'clear': // 清空
                    if(rndId === 999)
                    {
                        trnds = [];
                        tempLay.layouts = [];
                        this.props.updateTempLays(tempLays);
                        this.setState({rnds:trnds});
                    }

                    break;
                case 'parse': // 粘贴
                    break;
                default:
                    break;
            }
        });
        Bus.addListener('alignment',(type,sRndId,tRndId) => {
        	if(sRndId === tRndId)
        		return;
        	let rnds = this.state.rnds;
        	let sRnd,tRnd,sIndex;
        	for(let i=0;i<rnds.length;i++){
        		if(rnds[i].id === sRndId){
                    sRnd = rnds[i];
                    sIndex = i;
				}
        		if(rnds[i].id === tRndId)
        			tRnd = rnds[i];
			}
			let mark = false;
			switch (type) {
				case 'left':
					if(sRnd.width+tRnd.posX+tRnd.width > this.state.touchWidth){
                        Modal.warning({
                            title: '提示',
                            centered: true,
                            content: '越界',
                        });
                        return;
					}
					sRnd.ref.updatePosition({
						x:this.state.deltaX+tRnd.posX+tRnd.width,
						y:this.state.deltaY+tRnd.posY,
					})
					sRnd.enableDrag = true;
					sRnd.posX = tRnd.posX+tRnd.width;
					sRnd.posY = tRnd.posY;
					mark = true;
					break;
                case 'top':
                    if(sRnd.height+tRnd.posY+tRnd.height > this.state.touchHeight){
                        Modal.warning({
                            title: '提示',
                            centered: true,
                            content: '越界',
                        });
                        return;
                    }
                    sRnd.ref.updatePosition({
                        x:this.state.deltaX+tRnd.posX,
                        y:this.state.deltaY+tRnd.posY+tRnd.height,
                    })
                    sRnd.enableDrag = true;
                    sRnd.posX = tRnd.posX;
                    sRnd.posY = tRnd.posY+tRnd.height;
                    mark = true;
                    break;
                case 'right':
                    if(sRnd.width > tRnd.posX){
                        Modal.warning({
                            title: '提示',
                            centered: true,
                            content: '越界',
                        });
                        return;
                    }
                    sRnd.ref.updatePosition({
                        x:tRnd.posX-sRnd.width+this.state.deltaX,
                        y:tRnd.posY+this.state.deltaY,
                    })
                    sRnd.enableDrag = true;
                    sRnd.posX = tRnd.posX - sRnd.width;
                    sRnd.posY = tRnd.posY;
                    mark = true;
                    break;
                case 'buttom':
                    if(sRnd.height > tRnd.posY){
                        Modal.warning({
                            title: '提示',
                            centered: true,
                            content: '越界',
                        });
                        return;
                    }
                    sRnd.ref.updatePosition({
                        x:tRnd.posX+this.state.deltaX,
                        y:tRnd.posY-sRnd.height+this.state.deltaY,
                    })
                    sRnd.enableDrag = true;
                    sRnd.posX = tRnd.posX;
                    sRnd.posY = tRnd.posY-sRnd.height;
                    mark = true;
                    break;
				default:
					break;
			}
			if(mark){
                rnds[sIndex] = sRnd;
                this.setState({rnds:rnds})
			}

		});

        // 接收Nature.js组件消息
        Bus.addListener('changeRndInfo', (type,val) => {

            let rnds = this.state.rnds;
            let tempLays = this.props.tempLays;
            let index = this.state.rndClickIndex;

            let srnd = rnds[index];
            let tempRnd = tempLays[this.props.activeId].layouts[index];

            if(type === "posX")
            {
                val = val/1920 * this.state.touchWidth;
                let posX = val + this.state.deltaX;
                let posY = (tempRnd.pos.y * this.state.touchHeight)+this.state.deltaY;
                srnd.ref.updatePosition({x:posX,y:posY});
                tempRnd.pos.x = val/this.state.touchWidth;
            }
            if(type === "posY")
            {
                val = val/1080 * this.state.touchHeight;
                let posX = (tempRnd.pos.x * this.state.touchWidth)+this.state.deltaX;
                let posY = val + this.state.deltaY;
                srnd.ref.updatePosition({x:posX,y:posY});
                tempRnd.pos.y = val/this.state.touchHeight;
            }

            if(type === "width")
            {
                val = val/1920 * this.state.touchWidth;
                let ww = val;
                let hh = tempRnd.pos.h * this.state.touchHeight;
                srnd.ref.updateSize({width:ww,height:hh});
                tempRnd.pos.w = val/this.state.touchWidth;

            }
            if(type === "height")
            {
                val = val/1080 * this.state.touchHeight;
                let ww = tempRnd.pos.w * this.state.touchWidth;
                let hh = val;
                srnd.ref.updateSize({width:ww,height:hh});
                tempRnd.pos.h = val/this.state.touchHeight;

            }
            this.props.updateTempLays(tempLays);
        });

        //接收Pattern.js组件消息
        Bus.addListener("clearLayout",()=>{
            this.setState({rnds:[]});
        })

        //接收Pattern.js组件消息
        Bus.addListener("handleProperty",(mark,val)=>{

            let tempLays = this.props.tempLays;
            let tempLay = tempLays[this.props.activeId];
            let trnds = tempLay.layouts;

            // 0:无网格 1:5x5 2:二等分 3:四等分 4:左二右一 5:左三右一 6:左一右二 7:左一右三
            if(mark === "grid") {
                this.setState({
                    gridId:val
                })
            }

            if(mark === "clear") {
                trnds = [];
                tempLay.layouts = [];
                this.props.updateTempLays(tempLays);
                this.setState({rnds:trnds});
            }

            if(mark === "scale") {
                let index = this.state.rndClickIndex;
                trnds[index].lock = val;
                tempLays[this.props.activeId].layouts = trnds;
                this.props.updateTempLays(tempLays);
                this.setState({rnds:trnds})
            }

            if(mark === "delete") {

                let trndList = [];
                for(let i=0;i<trnds.length;i++){
                    if(i !== this.state.rndClickIndex){
                        trndList.push(trnds[i]);
                    }
                }
                tempLays[this.props.activeId].layouts = trndList;
                this.props.updateTempLays(tempLays);

                this.setState({
                    rnds:trndList
                })
            }

            if(mark === "upIndex") {

                let index = -1;
                for(let i=0;i<trnds.length;i++){
                    if(trnds[i].pos.index > index)
                        index = trnds[i].pos.index;
                }

                index++;
                trnds[this.state.rndClickIndex].pos.index = index;
                this.setState({rnds:trnds});
                tempLay.layouts = trnds;
                this.props.updateTempLays(tempLays);
            }

            if(mark === "downIndex") {
                let rnds = this.state.rnds;
                trnds[this.state.rndClickIndex].pos.index = 0;
                this.setState({rnds:trnds});
                tempLay.layouts = trnds;
                this.props.updateTempLays(tempLays);
            }

            if(mark === "replace"){
                let typeList = val.split("_");
                let id = typeList[0];
                let name = typeList[1];
                let enable = typeList[2];

                trnds[this.state.rndClickIndex].id = id;
                trnds[this.state.rndClickIndex].name = name;
                trnds[this.state.rndClickIndex].type = name;
                trnds[this.state.rndClickIndex].enable = enable;
                trnds[this.state.rndClickIndex].key = Math.round(Math.random()*100000);
                this.setState({rnds:trnds});
                tempLay.layouts = trnds;
                this.props.updateTempLays(tempLays);
            }

        })
	}

	render() {
		const { canDrop, isOver, connectDropTarget,isOverCurrent,greedy } = this.props;
        let backgroundColor = '#333';
		// 	backgroundColor = 'black';
		// 拖拽组件此时正处于目标区域时，当前组件背景色变为黑色
        if (isOverCurrent || (isOver && greedy && canDrop))
            backgroundColor = 'black';

		let sy = {
			backgroundColor: backgroundColor,
			width: this.state.touchWidth+'px',
			height: this.state.touchHeight+'px'
		}

		// 使用 connectDropTarget 包裹住 DOM 节点，使其可以接收对应的 drag source 组件
		// connectDropTarget 包裹住的 DOM 节点才能接收 drag source 组件
		return connectDropTarget && connectDropTarget(
			<div>
				<div
					className={
						!this.state.click ? style["wch-container"] : [`${style["wch-container"]}`,`${style["active"]}`].join(' ')
					}
					 style={sy}
					onContextMenu={(e)=>{
						this.onTouchClick(e);
					}}
					onClick={(e)=>{this.onTouchClick(e)}}>

                    <div className={
                        this.state.gridId === "1" ? style["wch-grid-line"] : [`${style["wch-grid-line"]}`,`${style.hide}`].join(' ')
                    }>
                        <Grid8x8 />
                    </div>

                    <div className={
                        this.state.gridId === "2" ? style["wch-grid-line"] : [`${style["wch-grid-line"]}`,`${style.hide}`].join(' ')
                    }>
                        <Grid3x3 />
                    </div>

                    <div className={
                        this.state.gridId === "3" ? style["wch-grid-line"] : [`${style["wch-grid-line"]}`,`${style.hide}`].join(' ')
                    }>
                        <Grid2x2 />
                    </div>

                    <div className={
                        this.state.gridId === "4" ? style["wch-grid-line"] : [`${style["wch-grid-line"]}`,`${style.hide}`].join(' ')
                    }>
                        <Grid2Split />
                    </div>
                    <div className={
                        this.state.gridId === "5" ? style["wch-grid-line"] : [`${style["wch-grid-line"]}`,`${style.hide}`].join(' ')
                    }>
                        <Grid2And1 />
                    </div>
                    <div className={
                        this.state.gridId === "6" ? style["wch-grid-line"] : [`${style["wch-grid-line"]}`,`${style.hide}`].join(' ')
                    }>
                        <Grid1And2 />
                    </div>
                    <div className={
                        this.state.gridId === "7" ? style["wch-grid-line"] : [`${style["wch-grid-line"]}`,`${style.hide}`].join(' ')
                    }>
                        <Grid3And1 />
                    </div>
                    <div className={
                        this.state.gridId === "8" ? style["wch-grid-line"] : [`${style["wch-grid-line"]}`,`${style.hide}`].join(' ')
                    }>
                        <Grid1And3 />
                    </div>
					{this.state.rnds.map((rnd,index)=>{
						return (
							<Rnd
								className={
									!rnd.click ? style["wch-rnd-wrap"] : [`${style["wch-rnd-wrap"]}`,`${style["active"]}`].join(' ')
								}
                                style={{zIndex:rnd.pos.index}}
                                key={rnd.key}
                                //key={Math.round(Math.random()*100000)}
								default={{
                                    x: rnd.pos.x * this.state.touchWidth,
                                    y: rnd.pos.y * this.state.touchHeight,
                                    width: rnd.pos.w * this.state.touchWidth,
                                    height: rnd.pos.h * this.state.touchHeight
								}}
								onResize={(event,direction,ele,delta,pos)=>{

									let srnd = this.state.rnds[index];
                                    srnd.pos.w = (this.props.tempLays[this.props.activeId].layouts[index].pos.w*this.state.touchWidth + delta.width)/this.state.touchWidth;
                                    srnd.pos.h = (this.props.tempLays[this.props.activeId].layouts[index].pos.h*this.state.touchHeight + delta.height)/this.state.touchHeight;
                                    srnd.pos.x = pos.x/this.state.touchWidth;
                                    srnd.pos.y = pos.y/this.state.touchHeight;

									// 与Nature.js组件通信
									Bus.emit('showNature', srnd, index);
								}}
								onResizeStop={(event,direction,ele,delta,pos)=>{

									// 拖拽停止
									let rnds = this.state.rnds;
									let srnd = rnds[index];

                                    srnd.pos.w = (this.props.tempLays[this.props.activeId].layouts[index].pos.w*this.state.touchWidth + delta.width)/this.state.touchWidth;
                                    srnd.pos.h = (this.props.tempLays[this.props.activeId].layouts[index].pos.h*this.state.touchHeight + delta.height)/this.state.touchHeight;
                                    srnd.pos.x = pos.x/this.state.touchWidth;
                                    srnd.pos.y = pos.y/this.state.touchHeight;

                                    let tempLays = this.props.tempLays;
                                    tempLays[this.props.activeId].layouts[index].pos = srnd.pos;
                                    this.props.updateTempLays(tempLays)

									// 与Nature.js组件通信
                                    Bus.emit('showNature', srnd, index);
								}}
                                enableResizing={index === this.state.rndClickIndex}
								onDrag={(event,data)=>{
									// 拖动停止
									let srnd = this.state.rnds[index];
                                    srnd.pos.x =(data.lastX-this.state.deltaX)/this.state.touchWidth;
                                    srnd.pos.y = (data.lastY-this.state.deltaY)/this.state.touchHeight;
									// 与Nature.js组件通信
                                    Bus.emit('showNature', srnd, index);
								}}
								onDragStop={(event,data)=>{
									// 拖动停止
									let srnds = this.state.rnds[index];

                                    srnds.pos.x =(data.lastX-this.state.deltaX)/this.state.touchWidth;
                                    srnds.pos.y = (data.lastY-this.state.deltaY)/this.state.touchHeight;

                                    let tempLays = this.props.tempLays;
                                    tempLays[this.props.activeId].layouts[index].pos = srnds.pos;
                                    this.props.updateTempLays(tempLays)
									// 与Nature.js组件通信
                                    Bus.emit('showNature', srnds, index);
								}}
								onClick={(e)=>(this.onRndClick(rnd.id,index,e))}
								onContextMenu={(e)=>{
									this.onRndClick(rnd.id,index,e);
								}}
                                // onDoubleClick={()=>{
                                 //    let rnds = this.state.rnds;
                                 //    // 需要拷贝对象，防止修改源数据
                                 //    let trnd = {...rnds[index]}
                                 //    trnd.enableDrag = !trnd.enableDrag;
                                 //    rnds[index] = trnd;
                                 //    this.setState({rnds:rnds});
								// }}
								ref={(c)=>{
									let rnds = this.state.rnds;
									for(let i=0;i<rnds.length;i++){
										if(rnd.key === rnds[i].key){
											rnds[i].ref = c;
										}
									}
								}}
                                lockAspectRatio={rnd.lock}
                                //disableDragging={!rnd.enableDrag}
								bounds={'.'+style["wch-container"]}>

                                <Place name={rnd.name} click={rnd.click} rndId={rnd.id} enable={rnd.enable}></Place>

                                {/*{(rnd.type !== 'layout') ? (*/}
										{/*<Place name={rnd.type} click={rnd.click} rndId={rnd.id}></Place>*/}
									{/*) : (*/}
										{/*<Layout greedy={true}*/}
												{/*name={rnd.type}*/}
												{/*layWidth={rnd.width}*/}
												{/*layHeight={rnd.height}*/}
												{/*layX={rnd.posX}*/}
												{/*layY={rnd.posY}*/}
												{/*touchX={this.state.touchX}*/}
												{/*touchY={this.state.touchY}*/}
												{/*parentRndClick={this.state.rndClick}*/}
												{/*callback={this.onHandleLayout.bind(this)}></Layout>*/}
									{/*)*/}
                                {/*}*/}
                                {/*<Line name={'left'} rndId={rnd.id} show={!rnd.enableDrag}/>*/}
                                {/*<Line name={'top'} rndId={rnd.id} show={!rnd.enableDrag}/>*/}
                                {/*<Line name={'right'} rndId={rnd.id} show={!rnd.enableDrag}/>*/}
                                {/*<Line name={'buttom'} rndId={rnd.id} show={!rnd.enableDrag}/>*/}
							</Rnd>
						)
					})}
				</div>
				<Menu
					bound={{
						startX: this.state.touchX,
						startY: this.state.touchY + this.state.deltaY + (this.state.bodyHeight - 32)*0.05,
						endX: this.state.touchX + this.state.touchWidth,
						endY: this.state.touchY + this.state.touchHeight + this.state.deltaY + (this.state.bodyHeight - 32)*0.05
					}}
				  	//callback={this.onHandleMenu.bind(this)}
				/>
			</div>
		);
	}
}

const stateToProps = (state) => {
    return {
        defLays: state.defLays,
        activeId: state.activeId,
        tempLays: state.tempLays,
        configs: state.configs,
    }
}

const dispatchTOProps = (dispatch) => {
    return {
        updateLayout(lays) {
            let action = {
                type: 'updateDefLays',
                value: lays
            }
            dispatch(action)
        },
        updateTempLays(lays) {
            let action = {
                type: 'updateTempLays',
                value: lays
            }
            dispatch(action)
        },
        updateConfigs(confs) {
        let action = {
            type: 'updateConfigs',
            value: confs
        }
        dispatch(action)
    }
    }
}

export default connect(stateToProps, dispatchTOProps)(Touch);
