import style from '../../../css/view/nature.module.less'
import React from "react";
import { Table,Checkbox,Select,Input } from "antd";
import { Resizable } from 'react-resizable';
import Bus from "../../../util/events"
import { DeleteFilled,CaretUpOutlined,CaretDownOutlined } from "@ant-design/icons"

const { Option } = Select;


const ResizeableTitle = props => {
    const { onResize, width, ...restProps } = props;

    if (!width) {
        return <th {...restProps} />;
    }

    return (
        <Resizable
            width={width}
            height={0}
            handle={
                <span className={[`${style["react-resizable-handle"]}`,`${style["react-resizable-handle-ew"]}`].join(' ')}></span>
            }
            onResize={onResize}
            draggableOpts={{ enableUserSelectHack: false }}
        >
            <th {...restProps} />
        </Resizable>
    );
};

export default class Pattern extends React.Component {
    state = {
        columns: [
            {
                title: ()=>{
                    return (
                        <div>
                            <cn>名称</cn>
                            <en>Name</en>
                        </div>
                    )
                },
                dataIndex: 'name',
                width: 60,
                render:(text,record,index)=>{
                    if(record.mark === "grid"){
                        return (
                            <div>
                                <cn>显示网格</cn>
                                <en>Grid</en>
                            </div>
                        )
                    }
                    if(record.mark === "clear"){
                        return (
                            <div>
                                <cn>清空布局</cn>
                                <en>Clear</en>
                            </div>
                        )
                    }
                    if(record.mark === "scale"){
                        return (
                            <div>
                                <cn>比例缩放</cn>
                                <en>Scale</en>
                            </div>
                        )
                    }
                    if(record.mark === "delete"){
                        return (
                            <div>
                                <cn>删除模块</cn>
                                <en>Delete</en>
                            </div>
                        )
                    }
                    if(record.mark === "upIndex"){
                        return (
                            <div>
                                <cn>置顶模块</cn>
                                <en>Top</en>
                            </div>
                        )
                    }
                    if(record.mark === "downIndex"){
                        return (
                            <div>
                                <cn>置底模块</cn>
                                <en>Bottom</en>
                            </div>
                        )
                    }
                    if(record.mark === "replace"){
                        return (
                            <div>
                                <cn>更换模块</cn>
                                <en>Replace</en>
                            </div>
                        )
                    }
                }
            },
            {
                title: ()=>{
                    return (
                        <div>
                            <cn>操作</cn>
                            <en>Operate</en>
                        </div>
                    )
                },
                dataIndex: 'value',
                width: 100,
                render:(text,record,index)=>{
                    let mark = record.mark;
                    if(mark === "grid")
                    {
                        return (
                            <Select key={index} defaultValue="none" style={{ width: 100 }} size={"small"}
                                    onChange={val=>{
                                        this.handleProperty("grid",val);
                                    }}
                            >
                                <Option value="0">none</Option>
                                <Option value="1">8x8</Option>
                                <Option value="2">3x3</Option>
                                <Option value="3">2x2</Option>
                                <Option value="4">1+1</Option>
                                <Option value="5">1+2</Option>
                                <Option value="6">2+1</Option>
                                <Option value="7">1+3</Option>
                                <Option value="8">3+1</Option>
                            </Select>
                        )
                    }

                    if(mark === "clear")
                        return <DeleteFilled onClick={()=>{this.handleProperty("clear")}}/>

                    if(mark === "replace"){
                        return (
                            <Select key={index} value={this.state.activeItem} style={{ width: 100 }} size={"small"}
                                    onChange={val=>{
                                        this.handleProperty("replace",val);
                                    }}
                            >
                                {
                                    text.map((item,index)=>{
                                        if(item.type !== "file") {
                                            return(
                                                <Option key={index} value={item.id+"_"+item.name+"_"+item.enable}>{item.name}</Option>
                                            )
                                        }
                                    })
                                }
                            </Select>
                        )
                    }
                    if(mark === "delete"){
                        return <DeleteFilled onClick={()=>{this.handleProperty("delete")}}/>
                    }

                    if(mark === "scale")
                    {
                        return <Checkbox checked={text}
                                         onChange={
                                            e =>{
                                              let check = e.target.checked;
                                                let data = this.state.data;
                                                for(let i=0;i<data.length;i++) {
                                                    let dd = data[i];
                                                    if(dd.mark === "scale") {
                                                        dd.value = check;
                                                        data[i] = dd;
                                                    }
                                                }
                                                this.setState({data:data});
                                              this.handleProperty("scale",check);
                                            }
                                        }/>
                    }

                    // if(mark === "color")
                    //     return <div style={{background:"lightseagreen",width:"50px",height:"15px"}}></div>

                    if(mark === "upIndex")
                        return <CaretUpOutlined onClick={()=>{this.handleProperty("upIndex")}}></CaretUpOutlined>

                    if(mark === "downIndex")
                        return <CaretDownOutlined onClick={()=>{this.handleProperty("downIndex")}}></CaretDownOutlined>


                }
            }
        ],
        data:[],
        data1: [
            {
                key: 0,
                name: "显示网格",
                value: 'container',
                mark: 'grid',
            },
            {
                key: 1,
                name: '清空布局',
                value: 'container',
                mark: 'clear',
            },
        ],
        data2: [
            {
                key: 1,
                name: '比例缩放',
                value: '-',
                mark: 'scale',
            },
            {
                key: 2,
                name: '删除模块',
                value: '-',
                mark: 'delete',
            },
            {
                key: 3,
                name: '置顶模块',
                value: '-',
                mark: 'upIndex',
            },
            {
                key: 4,
                name: '置底模块',
                value: '-',
                mark: 'downIndex',
            },
            {
                key: 0,
                name: '更换模块',
                value: '-',
                mark: 'replace',
            },
        ],
        activeItem:"",
    };

    components = {
        header: {
            cell: ResizeableTitle,
        },
    };




    handleResize = index => (e, { size }) => {
        this.setState(({ columns }) => {
            const nextColumns = [...columns];
            nextColumns[index] = {
                ...nextColumns[index],
                width: size.width,
            };
            return { columns: nextColumns };
        });
    };

    handleProperty = (mark,val) =>{
        if(mark === "replace") {
            let typeList = val.split("_");
            let name = typeList[1];
            this.setState({
                activeItem: name
            })
        }
        //与Touch.js组件通信
        Bus.emit("handleProperty",mark,val);
    }

    clearLayout = () => {
        //与Touch.js组件通信
        Bus.emit("clearLayout");
    }

    componentDidMount(){
        // 接收Touch.js组件消息
        this.setState({data:this.state.data1})
        this.eventEmitter = Bus.addListener('showPattern', (nature,index,touchW,touchH,configs) => {
            let data;
            if(index >= 0){
                data = this.state.data2;
                for(let i=0;i<data.length;i++){
                    if(data[i].mark === "scale")
                        data[i].value = nature.lock;
                    if(data[i].mark === "replace")
                        data[i].value = configs;
                }
            }
            else
                data = this.state.data1;
            this.setState({
                data: data,
                activeItem: nature.name
            })
        });
    }

    render() {
        const columns = this.state.columns.map((col, index) => ({
            ...col,
            onHeaderCell: column => ({
                width: column.width,
                onResize: this.handleResize(index),
            }),
        }));

        return <Table
            className={style.table}
            bordered
            pagination={false}
            size={'small'}
            rowClassName={(record, index) => { return index % 2 === 1?style.odd:style.enev }}
            components={this.components}
            columns={columns}
            dataSource={this.state.data} />;
    }
}