import style from '../../../css/view/menu.module.less'
import React from 'react'
import { DeleteOutlined,CaretUpOutlined,CaretDownOutlined,BlockOutlined,SnippetsOutlined,CopyOutlined } from '@ant-design/icons'
import Bus from "../../../util/events"


class Menu extends React.Component {
    state = {
        visible: false,
        rndId:-1
    };

    componentDidMount() {
        document.addEventListener('contextmenu', this.onHandleContextMenu);
        document.addEventListener('click', this.onHandleClick);
        document.addEventListener('scroll', this.onHandleScroll);

        // 接收Touch.js的消息
        Bus.addListener('showMenu', (rndId) => {
            this.setState({
                rndId: rndId
            })
        });
    };

    componentWillUnmount() {
        document.removeEventListener('contextmenu', this.onHandleContextMenu);
        document.removeEventListener('click', this.onHandleClick);
        document.removeEventListener('scroll', this.onHandleScroll);
    }

    onHandleContextMenu = (event) => {
        let boundStartX = this.props.bound.startX;
        let boundStartY = this.props.bound.startY;
        let boundEndX = this.props.bound.endX;
        let boundEndY = this.props.bound.endY;

        const clickX = event.clientX;
        const clickY = event.clientY;

        if(clickX>= boundStartX && clickX <= boundEndX && clickY >= boundStartY && clickY <= boundEndY)
        {
            event.preventDefault();
            this.setState({ visible: true });
            const screenW = window.innerWidth;
            const screenH = window.innerHeight;
            const rootW = this.root.offsetWidth;
            const rootH = this.root.offsetHeight;

            const right = (screenW - clickX) > rootW;
            const left = !right;
            const top = (screenH - clickY) > rootH;
            const bottom = !top;

            if (right) {
                this.root.style.left = `${clickX + 5}px`;
            }

            if (left) {
                this.root.style.left = `${clickX - rootW - 5}px`;
            }

            if (top) {
                this.root.style.top = `${clickY + 5}px`;
            }

            if (bottom) {
                this.root.style.top = `${clickY - rootH - 5}px`;
            }
        }
    };

    onHandleClick = (event) => {
        const { visible } = this.state;
        const wasOutside = !(event.target.contains === this.root);
        if (wasOutside && visible) this.setState({ visible: false, });
    };

    onHandleScroll = () => {
        const { visible } = this.state;
        if (visible) this.setState({ visible: false, });
    };

    onItemClick = (type,rndId) =>{
        //this.props.callback(type,index);
        Bus.emit('handleMenu', type,rndId);
    }

    render() {
        const { visible,rndId } = this.state;
        let content;
        if(rndId !== 999) {
            content = (
                <ul className={style["wch-container-menu"]}>
                    <li className={style["wch-container-menu-option"]}
                    onClick={()=>(this.onItemClick('delete',rndId))}>
                        <DeleteOutlined />
                        <span>删除模块</span>
                    </li>
                    <li className={style["wch-container-menu-option"]}
                        onClick={()=>(this.onItemClick('up',rndId))}>
                        <CaretUpOutlined />
                        <span>置顶模块</span>
                    </li>
                    <li className={style["wch-container-menu-option"]}
                        onClick={()=>(this.onItemClick('down',rndId))}>
                        <CaretDownOutlined />
                        <span>置底模块</span>
                    </li>
                    {/*<li className={style["wch-container-menu-option"]}*/}
                        {/*onClick={()=>(this.onItemClick('align',rndId))}>*/}
                        {/*<BlockOutlined  />*/}
                        {/*<span>快速对齐</span>*/}
                    {/*</li>*/}
                    {/*<li className={style["wch-container-menu-option"]}*/}
                        {/*onClick={()=>(this.onItemClick('copyAttr',rndId))}>*/}
                        {/*<CopyOutlined  />*/}
                        {/*<span>复制属性</span>*/}
                    {/*</li>*/}
                    {/*<li className={style["wch-container-menu-option"]}*/}
                        {/*onClick={()=>(this.onItemClick('parseAttr',rndId))}>*/}
                        {/*<SnippetsOutlined  />*/}
                        {/*<span>粘贴属性</span>*/}
                    {/*</li>*/}
                </ul>
            )
        } else {
            content = (
                <ul className={style["wch-container-menu"]}>
                    <li className={style["wch-container-menu-option"]}
                        onClick={()=>(this.onItemClick('clear',rndId))}>
                        <DeleteOutlined />
                        <span>清空</span>
                    </li>
                    {/*<li className={style["wch-container-menu-option"]}*/}
                        {/*onClick={()=>(this.onItemClick('parse',rndId))}>*/}
                        {/*<SnippetsOutlined />*/}
                        {/*<span>粘贴</span>*/}
                    {/*</li>*/}
                </ul>
            )
        }
        return(visible || null) &&
            <div ref={ref => {this.root = ref}} className={style["wch-container"]}>
                {content}
            </div>
    };
}

export default Menu;