import React from 'react';
import PropTypes from 'prop-types';
import { DragSource } from 'react-dnd';

const boxSource = {
	/**
	 * 开始拖拽时触发当前函数
	 * @param {*} props 组件的 props
	 */
	beginDrag(props) {
		// 返回的对象可以在 monitor.getItem() 中获取到
		return {
			name: props.name,
            rndId: props.rndId,
		}
	},
}

@DragSource(
	// type 标识，这里是字符串 'box'
	//ItemTypes.BOX,
	'line',
	// 拖拽事件对象
	boxSource,
	// 收集功能函数，包含 connect 和 monitor 参数
	// connect 里面的函数用来将 DOM 节点与 react-dnd 的 backend 建立联系
	(connect, monitor) => ({
		// 包裹住 DOM 节点，使其可以进行拖拽操作
		connectDragSource: connect.dragSource(),
		// 是否处于拖拽状态
		isDragging: monitor.isDragging()
	}),
)
class Line extends React.Component {

	static propTypes = {
		name: PropTypes.string.isRequired,
		isDragging: PropTypes.bool.isRequired,
		connectDragSource: PropTypes.func.isRequired
	}

	render() {
		const { name, show, isDragging, connectDragSource } = this.props
		let style;
		if(name === "left"){
            style = {
            	position: 'absolute',
                border: '1px solid gray',
                backgroundColor: '#333',
                cursor: 'move',
                height: '100%',
                width: '2px',
                left: '-15px',
            }
		}

		if(name === "top") {
            style = {
                position: 'absolute',
                border: '1px solid gray',
                backgroundColor: '#333',
                cursor: 'move',
                height: '2px',
                width: '100%',
                top: '-15px'
            }
		}

        if(name === "right"){
            style = {
                position: 'absolute',
                border: '1px solid gray',
                backgroundColor: '#333',
                cursor: 'move',
                height: '100%',
                width: '2px',
                right: '-15px'
            }
        }

        if(name === "buttom") {
            style = {
                position: 'absolute',
                border: '1px solid gray',
                backgroundColor: '#333',
                cursor: 'move',
                height: '2px',
                width: '100%',
                bottom: '-15px'
            }
        }

        const opacity = isDragging ? 0.4 : 1;
		const display = show ? 'block' : 'none';
		return connectDragSource && connectDragSource(
				<div style={{ ...style, opacity, display }}></div>
			);
	}
}

export default Line;