/* eslint-disable no-restricted-globals */
import JsonRpcClient from './rpcclient';
import hostIp from "./host"


const rpcc = (func, params, callbak, usrdata, jsonrpc) => {
    if(params===undefined || params===null)
        params=[];

    if(callbak!==undefined)
        jsonrpc.call(func, params, function(data){callbak(data,usrdata);}, function(res){console.log(res)});
    else
        jsonrpc.call(func, params, function(data){}, function(res){console.log(res)});
}

const rpc = (func, params, callbak, usrdata) => {
    let jsonrpc = new JsonRpcClient({ ajaxUrl: 'http://' + hostIp() + '/RPC' });
    rpcc(func, params, callbak, usrdata, jsonrpc);
}

export default rpc