import React from 'react'
import { Row, Col, Modal, Input, message} from 'antd';
import Bus from "../../../util/events"
import cookie from "react-cookies"

export default class AddModal extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            visible: false,
            layName: ""
        }
    }

    onHideModal = () => {
        this.setState({
            visible: false,
            layName:""
        });
    };

    onChange = e => {
        const { value } = e.target;
        this.setState({
            layName:value
        })
    }

    onClickOk = () => {
        let layName = this.state.layName;
        let lang = cookie.load("lang");
        let content = "请输入布局名";
        if(lang === "en"){
            content = "Please enter a layout name";
        }
        if(layName === "") {
            message.error(content,3);
            return;
        }

        // 与SIndex.js通信
        Bus.emit("addLayName",layName);
        this.onHideModal();
    }

    componentDidMount(){
        Bus.addListener("showAddModal",(key)=>{
            this.setState({
                visible: true,
            })
        })
    }

    render(){
        let lang = cookie.load("lang");
        let title = "请输入布局名称";
        let okText = "确定";
        let cancelText = "取消";
        let placeholder = "布局名称";
        if(lang === "en"){
            title = "Layout Name";
            okText = "OK";
            cancelText = "Cancel";
            placeholder = "please enter a layout name";
        }
        return(
            <Modal
                title={title}
                visible={this.state.visible}
                onOk={this.onClickOk}
                onCancel={this.onHideModal}
                okText={okText}
                cancelText={cancelText}
                destroyOnClose={true}
                width={"25%"}
                centered={true}
            >
                <Row>
                    <Col span={18} offset={3}>
                        <Input
                            type={"text"}
                            value={this.state.layName}
                            onChange={this.onChange}
                            placeholder={placeholder}
                        />
                    </Col>
                </Row>
            </Modal>
        )
    }
}