import style from '../../../css/view/setting.module.less';

import React from "react";
import { Resizable } from 'react-resizable';
import Nature from "./Nature";
import Pattern from "./Pattern";

import { Layout } from "antd";
const { Header,Content } = Layout;

export default class Setting extends React.Component{

    constructor(props) {
        super(props)
        this.state = {
            headerHeight:320
        }
    }

    render() {
        return (
            <Layout className={style["wch-container"]}>
                    <Resizable axis='y'
                               width={0}
                               height={this.state.headerHeight}
                               minConstraints={[0,150]}
                               handle={
                                   <span className={[`${style["react-resizable-handle"]}`,`${style["react-resizable-handle-ns"]}`].join(' ')}>
                                   </span>
                               }
                               onResize={(event, { element, size })=>{
                                   this.setState({headerHeight: size.height });
                               }}>
                        <Header className={style["wch-header-wrap"]} style={{height:this.state.headerHeight+'px'}}>
                            <Nature></Nature>
                        </Header>
                    </Resizable>

                    <Content className={style["wch-content-wrap"]}>
                        <Pattern></Pattern>
                    </Content>
            </Layout>
        )
    }
}